import React from 'react';
import { useDrop } from 'react-dnd';
import styles from '../styles/Droppable.module.css';

function Droppable({ accept, handleDrop, text, children, state, big, style, component }) {
  const [{ isOver, canDrop }, drop] = useDrop(
    () => ({
      accept,
      drop: (item, monitor) => {
        const dropTarget = monitor.getDropResult();
        if(dropTarget){
          dropTarget.classList.add('added');
        }
        handleDrop(item, monitor, state)},
      collect: (monitor) => ({
        isOver: !!monitor.isOver({ shallow: true }),
        canDrop: !!monitor.canDrop(),
      }),
    }),
    [state] // Dependency
  );

  const isActive = isOver && canDrop;
  // const border = isOver ? '1px solid red' : 'none'

  // let newChil = React.Children.map((elem)=> elem.classList.add('added'));

  return (
    <div className={`${styles.droppable} ${isActive && styles.over}  ${
      !isActive && canDrop && styles.can
    } ${big && styles.big} 
     
   `}
    style={style}
      ref={drop}
      
    >

      

      <div >{children} </div> 
    </div>
  );
}

export default Droppable;